"use client";

import { useParams } from "next/navigation";

export default function Page() {
  const params = useParams();
  const sessionId = params?.id;

  return (
    <main className="mt-[0px] min-h-[calc(100vh-80px)] overflow-y-auto py-6 px-8">
      <h1 className="text-2xl font-semibold mb-4">Test Session Details</h1>
      <p className="text-base mb-2">Session ID: {sessionId}</p>
      <p className="text-sm text-neutral-500">
        이 페이지는 아직 상세 UI가 구현되지 않은 임시 화면입니다.
      </p>
    </main>
  );
}
